<script>
import TheHeader from "./components/layouts/TheHeader.vue";
import TheSlider from "./components/layouts/TheSlider.vue";
import TheAbout from "./components/contents/TheAbout.vue";
import ThePortofolio from "./components/contents/ThePortofolio.vue";
import TheContactMe from "./components/contents/TheContactMe.vue";
import TheSkills from "./components/contents/TheSkills.vue";
import TheFooter from "./components/layouts/TheFooter.vue";

export default {
  components: {
    TheHeader,
    TheSlider,
    TheAbout,
    ThePortofolio,
    TheContactMe,
    TheSkills,
    TheFooter,
  },
  name: "App",
  data() {
    return {
      loaded: false,
    };
  },
  methods: {
    handleLoad() {
      setTimeout(() => {
        this.loaded = true;
      }, 100);
    },
  },
  created() {
    window.addEventListener("load", this.handleLoad);
  },
  unmounted() {
    window.removeEventListener("load", this.handleLoad);
  },
};
</script>

