<template>
  <div class="reveal">
    <slot></slot>
  </div>
</template>

<script>
import ScrollReveal from "scrollreveal";
export default {
  props: {
    delay: {
      type: Number,
      default: 300,
    },
    duration: {
      type: Number,
      default: 1000,
    },
    distance: {
      type: String,
      default: "0px",
    },
  },
  mounted() {
    ScrollReveal().reveal(".reveal", {
      delay: this.delay,
      duration: this.duration,
      distance: this.distance,
    });
  },
};
</script>
