<template>
  <div :class="{ 'is-fixed': fixed }">
    <div class="shane_tm_topbar">
      <div class="topbar_inner">
        <div class="logo">
          <a href="/">
            <img :src="logo" alt="partners brand" />
          </a>
        </div>
        <!-- End logo -->
        <div class="menu">
          <div class="anchor_nav">
            <a href="#home" :class="{ 'is-active': isHome }">Home</a>
            <a href="#about" :class="{ 'is-active': isAbout }">About</a>
            <a href="#portfolio" :class="{ 'is-active': isPortofolio }"
              >Portfolio</a
            >
            <!-- <a href="#news">News</a> -->
            <a href="#contact" :class="{ 'is-active': isContact }">Contact</a>
            <!-- <a
              class="white-fill-bg btn-outline btn_sm"
              href="javascript:void(0)"
            >
              Buy Now
            </a> -->
          </div>
        </div>
        <!-- End menu -->
      </div>
    </div>
    <!--  End shane_tm_topbar -->

    <!-- Start shane mobile menu -->
    <div class="shane_tm_mobile_menu">
      <div class="topbar_inner">
        <div class="container bigger">
          <div class="topbar_in">
            <div class="logo">
              <a href="/">
                <img :src="logo" alt="partners brand" />
              </a>
            </div>
            <!-- End logo -->
            <div class="my_trigger">
              <div
                class="hamburger hamburger--collapse-r"
                :class="{ 'is-active': active }"
              >
                <div
                  class="hamburger-box"
                  @click="active = !active"
                  :aria-pressed="active ? 'true' : 'false'"
                >
                  <div class="hamburger-inner"></div>
                </div>
              </div>
              <!-- End hamburger menu -->
            </div>
          </div>
        </div>
      </div>
      <div class="dropdown" :class="{ active: active }">
        <div class="container">
          <span
            class="close_menu"
            @click="active = !active"
            :aria-pressed="active ? 'true' : 'false'"
          >
            close
          </span>
          <div class="dropdown_inner">
            <ul class="anchor_nav">
              <li class="current">
                <a href="#home" @click="active = !active">
                  Home
                </a>
              </li>
              <li>
                <a href="#about" @click="active = !active">
                  About
                </a>
              </li>
              <li>
                <a href="#portfolio" @click="active = !active">
                  Portfolio
                </a>
              </li>
              <!-- <li>
                <a href="#news" @click="active = !active">
                  News
                </a>
              </li> -->
              <li>
                <a href="#contact" @click="active = !active">
                  Contact
                </a>
              </li>
              <!-- <li>
                <a
                  class="white-fill-bg btn-outline btn_sm"
                  target="_blank"
                  rel="noreferrer"
                  href="https://themeforest.net/"
                >
                  Buy Now
                </a>
              </li> -->
            </ul>
            <!-- End social share -->
          </div>
        </div>
        <!-- End container -->
      </div>
    </div>
    <!-- End shane mobile menu -->
  </div>
</template>

<script>
export default {
  data() {
    return {
      fixed: false,
      active: false,
      isHome: true,
      isAbout: false,
      isPortofolio: false,
      isContact: false,
      logo:
        "https://res.cloudinary.com/djsa72jl/image/upload/c_scale,w_585/v1633453196/logo_qg4o6d.png",
    };
  },
  methods: {
    handleScroll() {
      window.top.scrollY > 100 ? (this.fixed = true) : (this.fixed = false);
      window.top.scrollY >= 0 && window.top.scrollY < 844
        ? (this.isHome = true)
        : (this.isHome = false);
      window.top.scrollY >= 844 && window.top.scrollY < 1653
        ? (this.isAbout = true)
        : (this.isAbout = false);
      window.top.scrollY >= 1653 && window.top.scrollY < 3227
        ? (this.isPortofolio = true)
        : (this.isPortofolio = false);
      window.top.scrollY >= 3227
        ? (this.isContact = true)
        : (this.isContact = false);
    },
  },
  created() {
    window.addEventListener("scroll", this.handleScroll);
  },
  unmounted() {
    window.removeEventListener("scroll", this.handleScroll);
  },
};
</script>

<style lang="scss" scoped>
.logo {
  img {
    max-width: 80px;
  }
}
</style>
