<template>
  <div class="tokyo_tm_contact">
    <div class="fields">
      <Form class="contact_form" @submit="onSubmit">
        <div class="first">
          <ul>
            <li>
              <Field
                name="client_name"
                type="text"
                placeholder="Name"
                autocomplete="off"
                :rules="isRequired"
                spellcheck="false"
              />
              <error-message name="client_name"></error-message>
            </li>

            <li>
              <Field
                name="client_email"
                type="email"
                placeholder="Email"
                autocomplete="off"
                :rules="validateEmail"
                spellcheck="false"
              />
              <error-message name="client_email"></error-message>
            </li>

            <li>
              <Field
                name="message"
                placeholder="message"
                autocomplete="off"
                :rules="isRequired"
                as="textarea"
                spellcheck="false"
              />
              <error-message name="message"></error-message>
            </li>
          </ul>
        </div>
        <div class="tokyo_tm_button">
          <button type="submit" class="white-fill-bg fill-black">
            Send Message
          </button>
        </div>
      </Form>
    </div>
    <!-- END FIELDS -->
  </div>
</template>

<script>
import { Form, Field, ErrorMessage } from "vee-validate";
import Swal from "sweetalert";
import EmailJs from "emailjs-com";
export default {
  components: {
    Form,
    Field,
    ErrorMessage,
  },

  methods: {
    async onSubmit(value) {
      await EmailJs.send(
        "contact-form-mc",
        "contact-form",
        value,
        "user_n30ksK5rhsqBuq2N4jpAD"
      )
        .then(() => {
          Swal("Email Sent!", "", "success");
        })
        .catch((err) => {
          Swal("Opps...", err, "error");
        });
    },
    isRequired(value) {
      return value ? true : "This field is required";
    },

    validateEmail(value) {
      if (!value) {
        return "This field is required";
      }
      const regex = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i;
      if (!regex.test(value)) {
        return "This field must be a valid email";
      }
      return true;
    },
  },
};
</script>
